@font-face {
  font-family: 'pignose-calendar-icon';
  src: url("../fonts/pignose.calendar.eot?gpa4vl");
  src: url("../fonts/pignose.calendar.eot?gpa4vl#iefix") format("embedded-opentype"), url("../fonts/pignose.calendar.ttf?gpa4vl") format("truetype"), url("../fonts/pignose.calendar.woff?gpa4vl") format("woff"), url("../fonts/pignose.calendar.svg?gpa4vl#pignose.calendar") format("svg");
  font-weight: normal;
  font-style: normal;
}

.pignose-calendar .icon-arrow-left,
.pignose-calendar .icon-arrow-right {
  font-family: 'pignose-calendar-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
}

.pignose-calendar .icon-arrow-left:before {
  content: '\e90b';
}

.pignose-calendar .icon-arrow-right:before {
  content: '\e90a';
}

.pignose-calendar-wrapper {
  display: none;
  position: fixed;
  width: 80%;
  max-width: 360px;
  top: 50%;
  left: 50%;
  border-radius: 2px;
  z-index: 50001;
  overflow: hidden;
  box-shadow: 0 4px 16px #000000;
  transform: translate3d(0, 160px, 0);
  opacity: 0;
  transition: opacity 0.3s ease, transform 0.5s ease-out;
}

.pignose-calendar-wrapper.pignose-calendar-wrapper-active {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.pignose-calendar-wrapper .pignose-calendar {
  max-width: auto;
  width: 100%;
  border: none;
}

.pignose-calendar-wrapper .pignose-calendar .pignose-calendar-button-group {
  border-top: 1px solid #e2e2e2;
  overflow: hidden;
}

.pignose-calendar-wrapper .pignose-calendar .pignose-calendar-button-group .pignose-calendar-button {
  width: 50%;
  display: block;
  float: left;
  height: 3.2em;
  text-align: center;
  line-height: 3.2em;
  color: #333333;
  font-weight: 600;
  text-decoration: none;
  transition: background-color 0.3s ease;
  box-sizing: border-box;
}

.pignose-calendar-wrapper .pignose-calendar .pignose-calendar-button-group .pignose-calendar-button:hover {
  background-color: #efefef;
}

.pignose-calendar-wrapper .pignose-calendar .pignose-calendar-button-group .pignose-calendar-button-apply {
  color: #ffffff;
  background-color: #2fabb7;
}

.pignose-calendar-wrapper .pignose-calendar .pignose-calendar-button-group .pignose-calendar-button-apply:hover {
  background-color: #49c4d0;
}

.pignose-calendar-wrapper-overlay {
  background-color: #000000;
  opacity: 0;
  transition: opacity .3s ease;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 50000;
}

.pignose-calendar-wrapper-overlay.pignose-calendar-wrapper-overlay-active {
  opacity: .7;
}

.pignose-calendar {
  width: 80%;
  max-width: 360px;
  background-color: #ffffff;
  border: 1px solid #d8d8d8;
  font-size: 100%;
  margin: 0 auto;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.25);
}

.pignose-calendar .pignose-calendar-top {
  padding: 2.6em 0;
  background-color: #fafafa;
  border-bottom: 1px solid #e2e2e2;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.125);
  position: relative;
  overflow: hidden;
}

.pignose-calendar .pignose-calendar-top .pignose-calendar-top-date {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 1.8em 0;
  text-align: center;
  text-transform: uppercase;
  box-sizing: border-box;
}

.pignose-calendar .pignose-calendar-top .pignose-calendar-top-year,
.pignose-calendar .pignose-calendar-top .pignose-calendar-top-month {
  display: block;
  text-align: center;
}

.pignose-calendar .pignose-calendar-top .pignose-calendar-top-year {
  font-size: 115%;
  color: rgba(0, 0, 0, 0.5);
}

.pignose-calendar .pignose-calendar-top .pignose-calendar-top-month {
  margin-bottom: .4em;
  font-size: 130%;
  font-weight: 600;
}

.pignose-calendar .pignose-calendar-top .pignose-calendar-top-nav {
  display: inline-block;
  width: 1.6em;
  height: 1.6em;
  position: relative;
  z-index: 5;
  text-decoration: none;
}

.pignose-calendar .pignose-calendar-top .pignose-calendar-top-nav .pignose-calendar-top-value {
  display: inline-block;
  color: #777777;
  font-size: 115%;
  font-weight: 600;
  vertical-align: middle;
  margin-top: -10px;
}

.pignose-calendar .pignose-calendar-top .pignose-calendar-top-nav .pignose-calendar-top-icon {
  color: #555555;
  font-size: 160%;
}

.pignose-calendar .pignose-calendar-top .pignose-calendar-top-nav.pignose-calendar-top-prev {
  float: left;
  margin-left: 1.6em;
}

.pignose-calendar .pignose-calendar-top .pignose-calendar-top-nav.pignose-calendar-top-prev .pignose-calendar-top-value {
  margin-left: .2em;
}

.pignose-calendar .pignose-calendar-top .pignose-calendar-top-nav.pignose-calendar-top-next {
  float: right;
  margin-right: 1.6em;
}

.pignose-calendar .pignose-calendar-top .pignose-calendar-top-nav.pignose-calendar-top-next .pignose-calendar-top-value {
  margin-right: .2em;
}

.pignose-calendar .pignose-calendar-header {
  padding: 0 1.2em;
  margin-top: 1.2em;
  font-weight: 600;
  overflow: hidden;
}

.pignose-calendar .pignose-calendar-header .pignose-calendar-week {
  float: left;
  width: 14.28%;
  height: 2.8em;
  text-align: center;
  line-height: 2.8em;
  box-sizing: border-box;
}

.pignose-calendar .pignose-calendar-header .pignose-calendar-week.pignose-calendar-week-sun,
.pignose-calendar .pignose-calendar-header .pignose-calendar-week.pignose-calendar-week-sat {
  color: #fa4832;
}

.pignose-calendar .pignose-calendar-header .pignose-calendar-week:last-child {
  width: 14.32%;
}

.pignose-calendar .pignose-calendar-body {
  padding: 1.2em;
}

.pignose-calendar .pignose-calendar-body .pignose-calendar-row {
  overflow: hidden;
}

.pignose-calendar .pignose-calendar-unit {
  float: left;
  display: block;
  height: 3.8em;
  width: 14.28%;
  text-align: center;
  line-height: 2.8em;
  box-sizing: border-box;
}

.pignose-calendar .pignose-calendar-unit:last-child {
  width: 14.32%;
}

.pignose-calendar .pignose-calendar-unit .pignose-calendar-button-schedule-container {
  line-height: .5em;
}

.pignose-calendar .pignose-calendar-unit .pignose-calendar-button-schedule-container .pignose-calendar-button-schedule-pin {
  display: inline-block;
  background-color: #777777;
  width: .5em;
  height: .5em;
  border-radius: 50%;
  margin-right: .2em;
}

.pignose-calendar .pignose-calendar-unit .pignose-calendar-button-schedule-container .pignose-calendar-button-schedule-pin:last-child {
  margin-right: 0;
}

.pignose-calendar .pignose-calendar-unit a {
  display: inline-block;
  width: 2.4em;
  height: 2.4em;
  border-radius: 50%;
  color: #49c4d0;
  line-height: 2.4em;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.pignose-calendar .pignose-calendar-unit a:active {
  background-color: #d8d8d8;
}

.pignose-calendar .pignose-calendar-unit.pignose-calendar-unit-disabled a {
  opacity: .5;
