.vsc__wrapper {
  margin: 0 0 2em 0;
  background: #f7f7f7;
  border: 1px solid rgba(211, 127, 159, 0.5);
  box-shadow: 0pt 2px 5px rgba(105, 108, 109, 0.7), 0px 0px 8px 5px rgba(208, 223, 226, 0.4) inset;
  padding: 2em;
}

.vsc__wrapper .hide {
  display: none;
}

.vsc__wrapper .vsc__input_wrapper {
  display: flex;
}

.vsc__wrapper .vsc__input_wrapper img {
  box-shadow: none;
}

.vsc__wrapper .vsc__input_wrapper a {
  text-decoration: none;
  margin-left: 2em;
  box-shadow: none;
}

.vsc__wrapper .calendar {
  display: flex;
  width: 100%;
  align-items: center;
  background: #c75f85;
  padding: 8px 5px;
  color: #fff;
  border: 1px solid #d1698f;
  margin-bottom: 10px;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.9);
  -webkit-box-shadow: 0px 1px 6px 4px rgba(0, 0, 0, 0.07) inset, 0px 0px 0px 3px #fefefe, 0px 0px 3px 3px #d2d2d2;
  cursor: pointer;
}

.vsc__wrapper .calendar .vsc__date {
  font-size: 2em;
  font-weight: bold;
}

@media screen and (max-width: 480px) {
  .vsc__wrapper .calendar .vsc__date {
    font-size: 1.5em;
  }
}

@media screen and (max-width: 374px) {
  .vsc__wrapper .calendar .vsc__date {
    font-size: 1em;
  }
}

.vsc__wrapper .vsc__wprapper_info {
  display: none;
}

.vsc__wrapper .vsc__wprapper_info .vsc_item {
  padding: 1em 0;
  background: #c75f85;
  padding: 8px 5px;
  color: #fff;
  border: 1px solid #d1698f;
  margin-bottom: 10px;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 0px 1px 6px 4px rgba(0, 0, 0, 0.07) inset, 0px 0px 0px 3px #fefefe, 0px 0px 3px 3px #d2d2d2;
}

.vsc__wrapper .vsc__wprapper_info span {
  font-weight: bold;
}

#calculator h3::after {
  content: ' ';
  display: block;
  width: 100%;
  height: 2px;
  margin-top: 10px;
  background: -webkit-linear-gradient(left, rgba(211, 127, 159, 0) 0%, rgba(211, 127, 159, 0.8) 20%, #e2a6bd 53%, rgba(211, 127, 159, 0.8) 79%, rgba(211, 127, 159, 0) 100%);
}

#calculator .vsc__error {
  display: none;
  background: #CA0101;
  padding: 8px 5px;
  color: #fff;
  border: 1px solid #CA0101;
  margin-bottom: 10px;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.9);
  -webkit-box-shadow: 0 1px 6px 4px rgba(0, 0, 0, 0.07) inset, 0 0 0 3px #fefefe, 0 0 3px 3px #d2d2d2;
  cursor: pointer;
}

.pignose-calendar-wrapper .pignose-calendar-button.pignose-calendar-button-apply {
  background-color: #c75f85 !important;
}

.pignose-calendar-wrapper .pignose-calendar-unit-active a, .pignose-calendar-wrapper .pignose-calendar-unit-first-active a {
  background-color: #c75f85 !important;
  outline: none;
}

.pignose-calendar-wrapper .pignose-calendar-top {
  background-color: #c75f85 !important;
}
